import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import {
  BrowserRouter as Router,
} from 'react-router-dom';

import CheckoutForm from "./CheckoutForm";
import "../../Stripe.css";
import { useSelector } from "react-redux";
import { selectCurrentOrder } from "../order/orderSlice";

// Make sure to call loadStripe outside of a component’s render to avoid
// recreating the Stripe object on every render.
// This is your test publishable API key.
const stripePromise = loadStripe("pk_test_51QecYVQvDxHj3veCR4byEM4kRSuIn7n6qfRpMHbqRydEO849YHDFX7y0kR02malW5i6S5dQQTlDlLWpNgHvR5fOy00bqumGPTr");

export default function StripeCheckout() {
  const [clientSecret, setClientSecret] = useState("");
  const currentOrder = useSelector(selectCurrentOrder)

  useEffect(() => {
    // Create PaymentIntent as soon as the page loads
    fetch("/create-payment-intent", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ totalAmount:currentOrder.totalAmount }),
      metadata:{
        order_id:currentOrder.id
        //this info will go to stripe=>and then to our webhook
        //so we can conclude that payment was successful,even if client closes window after pay
      }
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, []);

  const appearance = {
    theme: 'stripe',
  };
  // Enable the skeleton loader UI for optimal loading.
  const loader = 'auto';

  return (
      <div className="Stripe">
        {clientSecret && (
          <Elements options={{clientSecret, appearance, loader}} stripe={stripePromise}>
            <CheckoutForm></CheckoutForm>
          </Elements>
        )}
      </div>
  
  );
}