import React from "react";
import { useState } from "react";
import { Link, Navigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  deleteItemFromCartAsync,
  selectCartLoaded,
  selectCartStatus,
  selectItems,
  updateCartAsync,
} from "./cartSlice";
import { discountedPrice } from "../../app/constants";
import { Grid } from "react-loader-spinner";
import Model from "../comon/Model";

const Cart = () => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(true);
  const CartLoaded = useSelector(selectCartLoaded)
  const status = useSelector(selectCartStatus);
  const [openModel,setOpenModel]=useState(null)

  const items = useSelector(selectItems);
  const totalAmount = items
    .reduce((amount, item) => discountedPrice(item.product) * item.quantity + amount, 0)
    .toFixed(2);
  const totalItems = items.reduce((total, item) => item.quantity + total, 0);

  const handleQuantity = (e, item) => {
    dispatch(updateCartAsync({id:item.id, quantity: +e.target.value }));
  };

  const handleRemove = (e, id) => {
    dispatch(deleteItemFromCartAsync(id));
  };

 
  return (
    <>
      {!items.length&&CartLoaded && <Navigate to="/" replace={true}></Navigate>}

      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8  bg-white">
        <h1 className="text-4xl font-bold tracking-tight text-gray-900 p-5 mt-12">
          Cart
        </h1>
        <div className=" border-gray-200 px-4 py-6 sm:px-6">
          {status === "loading" ? (
            <Grid
              visible={true}
              height="80"
              width="80"
              color="rgb(79,70,229)"
              ariaLabel="grid-loading"
              radius="12.5"
              wrapperStyle={{}}
              wrapperClass="grid-wrapper"
            />
          ) : null}
          <div className="flow-root">
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {items.map((item) => (
                <li key={item.id} className="flex py-6">
                  <div className="size-24 shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.thumbnail}
                      alt={item.product.title}
                      className="size-full object-cover"
                    />
                  </div>

                  <div className="ml-4 flex flex-1 flex-col">
                    <div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <h3>
                          <a href={item.product.id}>{item.product.title}</a>
                        </h3>
                        <p className="ml-4">${discountedPrice(item.product)}</p>
                      </div>
                      <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>
                    </div>
                    <div className="flex flex-1 items-end justify-between text-sm">
                      <div className="text-gray-500">
                        Qty
                        <select
                          className="mx-3"
                          onChange={(e) => handleQuantity(e, item)}
                          value={item.quantity}
                        >
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                          <option value="5">5</option>
                        </select>
                      </div>

                      <div className="flex">
                        <Model
                          title={`Delete ${item.product.title}`}
                          message="Are you sure you want to delete this cart item"
                          dangerOption="Delete"
                          cancelOption="cancel"
                          dangerAction={(e) => handleRemove(e, item.id)}
                          calcelAction={()=>setOpenModel(null)}
                          showModel={openModel === item.id}
                        ></Model>
                        <button
                          onClick={e=>{setOpenModel(item.id)}}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>

        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
          <div className="flex justify-between my-2 text-base font-medium text-gray-900">
            <p>Subtotal</p>
            <p>$ {totalAmount}</p>
          </div>
          <div className="flex justify-between my-2 text-base font-medium text-gray-900">
            <p>Total Items In Cart</p>
            <p>{totalItems} items</p>
          </div>
          <p className="mt-0.5  text-sm text-gray-500">
            Shipping and taxes calculated at checkout.
          </p>
          <div className="mt-6">
            <Link
              to="/checkout"
              className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
            >
              Checkout
            </Link>
          </div>
          <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
            <p>
              or{" "}
              <Link to="/">
                <button
                  type="button"
                  onClick={() => setOpen(false)}
                  className="font-medium text-indigo-600 hover:text-indigo-500"
                >
                  Continue Shopping
                  <span aria-hidden="true"> &rarr;</span>
                </button>
              </Link>
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Cart;
