import React, { useEffect, useState } from "react";
import { discountedPrice, ITEMS_PER_ } from "../../../app/constants";
import { useDispatch, useSelector } from "react-redux";
import { PencilIcon, EyeIcon,ArrowDownIcon,ArrowUpIcon } from "@heroicons/react/24/outline";
import {
  fetchAllOrdersAsync,
  selectOrders,
  selectTotalOrder,
  UpdatedOrderAsync,
} from "../../order/orderSlice";
import Pagination from "../../comon/Pagination";

const AdminOrder = () => {
  const [page, setPage] = useState(1);
  const [sort, setSort] = useState({});
  const [editableOrderId, setEditableOrderId] = useState(-1);
  const dispatch = useDispatch();
  const orders = useSelector(selectOrders);
  console.log(orders);

  const totalOrders = useSelector(selectTotalOrder);

  const handleShow = () => {
    console.log("handleshow");
  };
  const handleEdit = (item) => {
    setEditableOrderId(item.id);
  };
  const handleupdate = (e, order) => {
    const UpdatedOrder = { ...order, status: e.target.value };
    dispatch(UpdatedOrderAsync(UpdatedOrder));
    setEditableOrderId(-1);
  };

  const handlPage = (page) => {
    setPage(page);
  };

  const chooseColor = (status) => {
    switch (status) {
      case "pending":
        return "bg-purple-200 text-purple-600";

      case "dispatched":
        return "bg-yellow-200 text-yellow-600";

      case "delivered":
        return "bg-green-200 text-green-600";

      case "cancelled":
        return "bg-red-200 text-red-600";
      default:
    }
  };
  const handleSort = (sortOption) => {
    const sort = { _sort: sortOption.sort, _order: sortOption.order };
    console.log({ sort });
    setSort(sort);
  };

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_ };
    dispatch(fetchAllOrdersAsync({ sort, pagination }));
    //TODO:server will filter deleted products
  }, [dispatch, page, sort]);

  return (
    <>
      <div className="p-6 px-0">
        <table className=" w-full min-w-max table-auto text-left font-sans">
          <thead className=" bg-gray-300 ">
            <tr className="text-gray-700">
              <th
                className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50"
                onClick={(e) =>
                  handleSort({
                    sort: "id",
                    order: sort?._order === "asc" ? "desc" : "asc",
                  })
                }
              >
                #Order{'   '}
                {sort._sort === 'id' && (sort._order ==='asc'?(<ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                ) :
                (<ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>))}
              </th>
              <th className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50">
                Items
              </th>
              <th
                className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50"
                onClick={(e) =>
                  handleSort({
                    sort: "totalAmount",
                    order: sort?._order === "asc" ? "desc" : "asc",
                  })
                }
              >
                Total Amount{'   '}
                {sort._sort === 'totalAmount' && (sort._order ==='asc'?(<ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                ) :
                (<ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>))}
              </th>
              <th className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50">
                Shipping Address
              </th>
              <th className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50">
                Status
              </th>
              <th className="cursor-pointer border-y border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50">
                Action
              </th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr>
                <td className="p-4 border-b border-blue-gray-50">
                  <div className="flex items-center gap-3">
                    <div className="flex flex-col">
                      <p className="block antialiased font-sans text-sm leading-normal text-blue-gray-900 font-normal">
                        {order.id}
                      </p>
                    </div>
                  </div>
                </td>
                <td className="p-4 border-b border-blue-gray-50">
                  {order.items.map((item) => (
                    <div className="flex items-center gap-3">
                      <img
                        src={item.product.thumbnail}
                        alt={item.product.title}
                        className="inline-block relative object-cover object-center w-9 h-9  rounded-full"
                      />
                      <span>
                        {item.product.title} - #{item.quantity} - $
                        {discountedPrice(item.product)}
                      </span>
                    </div>
                  ))}
                </td>
                <td className="p-4 border-b border-blue-gray-50">
                  <div className="flex flex-col">
                    <p className="block antialiased font-sans text-sm leading-normal text-blue-gray-900 font-normal">
                      ${order.totalAmount}
                    </p>
                  </div>
                </td>
                <td className="p-4 border-b border-blue-gray-50">
                  <div className="">
                    <p className="block antialiased font-sans text-sm leading-normal text-blue-gray-900 font-normal">
                      <div>
                        <strong>{order.selectedAddress.name},</strong>
                      </div>
                      <div>{order.selectedAddress.street},</div>
                      <div>{order.selectedAddress.city},</div>
                      <div>{order.selectedAddress.pinCode},</div>
                      <div>{order.selectedAddress.phno},</div>
                    </p>
                  </div>
                </td>
                <td className="p-4 border-b border-blue-gray-50">
                  {order.id === editableOrderId ? (
                    <select onChange={(e) => handleupdate(e, order)}>
                      <option value="pending">Pending</option>
                      <option value="dispatched">Dispatched</option>
                      <option value="delivered">Delivered</option>
                      <option value="cancelled">Cancelled</option>
                    </select>
                  ) : (
                    <div className="w-max">
                      <span
                        className={`${chooseColor(
                          order.status
                        )}relative grid items-center font-sans font-bold uppercase whitespace-nowrap select-none py-1 px-2 text-xs rounded-3xl`}
                      >
                        {order.status}
                      </span>
                    </div>
                  )}
                </td>
                <td className="p-4 border-b border-blue-gray-50">
                  <button
                    className="relative align-middle select-none hover:text-purple-500 font-sans font-medium text-center uppercase transition-all disabled:opacity-50 disabled:shadow-none disabled:pointer-events-none w-10 max-w-[40px] h-10 max-h-[40px] rounded-lg text-xs text-blue-gray-500 hover:bg-blue-gray-500/10 active:bg-blue-gray-500/30"
                    type="button"
                  >
                    <EyeIcon
                      className="w-7 h-7"
                      onClick={(e) => handleShow(order)}
                    ></EyeIcon>
                  </button>
                  <button
                    className="relative align-middle select-none font-sans  hover:text-purple-500 font-medium text-center uppercase transition-all disabled:opacity-50 disabled:shadow-none disabled:pointer-events-none w-10 max-w-[40px] h-10 max-h-[40px] rounded-lg text-xs text-blue-gray-500 hover:bg-blue-gray-500/10 active:bg-blue-gray-500/30"
                    type="button"
                  >
                    <PencilIcon
                      className="w-7 h-7"
                      onClick={(e) => handleEdit(order)}
                    ></PencilIcon>
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <Pagination
          page={page}
          setPage={setPage}
          handlPage={handlPage}
          totalItems={totalOrders}
        ></Pagination>
      </div>
    </>
  );
};

export default AdminOrder;
